import { FileText, Icon, Pizza } from "lucide-react"
import { ReactNode } from "react"

type Step ={
    icon: ReactNode;
    label:string;
    description:string;
}
const steps: Step[] = [
    {
        icon: <FileText size ={64} strokeWidth={1.5}/>,
        label: 'Upload your PDF',
        description:'Simply drag and drop your PDF document or click to upload',
    },
    {
        icon: <Brain size ={64} strokeWidth={1.5}/>,
        label: 'Upload your PDF',
        description:'Simply drag and drop your PDF document or click to upload',
    },
    {
        icon: <FileText size ={64} strokeWidth={1.5}/>,
        label: 'Upload your PDF',
        description:'Simply drag and drop your PDF document or click to upload',
    },
];
export default function HowItWorksSection(){
    return (<section className="relative overflow-hidden bg-gray-50">
        <div className="py-12 lg:py-24 max-w-5xl mx-auto px-4 sm:px-6
        lg:px-8">
        
         <div
                aria-hidden="true"
                className="pointer-events-none absolute inset-x-0 -top-40
            -z-10 transform-gpu overflow-hidden blur-3xl sm:-top-30"
            >
                <div style={{
                    clipPath:
                        'polygon(50% 0%, 61% 35%, 98% 35%, 68% 57%, 79% 91%, 50% 70%, 21% 91%, 32% 57%, 2% 35%, 39% 35%)'
                }}
                    className="relative left-[calc(50%-11rem)] aspect-1155/678 w-[36,125rem] -translate-x-1/2 rotate-[30deg] bg-linear-to-r from-emerald-500 via-teal-500 to-cyan-500 opacity-20 sm:left-[calc(50%-30rem)] sm:w-[72rem]"

                />
            </div>

            <div className="text-center mb-16">
                <h2 className="font-bold text-xl uppercase mb-4 text-rose-500">How is works</h2>
                <h3 className="font-bold text-3xl max-w-2xl mx-auto">Transform any PDF into an easy-to-digest summary in three simples steps</h3>
            </div>

            <div className="grid grid-cols-1 md:cols-3 gap-8 max-w-6xl mx-auto relative">
                {[].map((step , idx) => (
                    <div></div>
                    ))}
            </div>
       </div>
    </section>
    );
}
